import { BaseElement } from "./BaseElement";
import { BpmnModel } from "./BpmnModel";
import { IClrTypeElement } from "./IClrTypeElement";

export abstract class EventDefinition extends BaseElement implements IClrTypeElement {

    public constructor(model: BpmnModel) {
        super(model);
    }

    abstract ClrTypeID: string;

    abstract NewInstance(): EventDefinition;
}